  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

ul, ol, menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

html {
  box-sizing: border-box;
  overflow: auto;
}

body {
  background-color: var(--bg);
  font-family: Montserrat, sans-serif;
}

